@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Golos";
  font-display: swap;
  src: url("../fonts/GolosText-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Golos";
  font-display: swap;
  src: url("../fonts/GolosText-Medium.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Golos";
  font-display: swap;
  src: url("../fonts/GolosText-Bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 1vw;
  line-height: 1.5rem;
  font-family: "Golos", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (max-width: 767px) {
  html {
    font-size: 5vw;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

[type=button]:focus-visible:focus-visible,
[type=reset]:focus-visible:focus-visible,
[type=submit]:focus-visible:focus-visible,
button:focus-visible:focus-visible,
a:focus-visible {
  outline: none;
  box-shadow: inset 0 0 0 1px #1E1E1E;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 6.25rem;
  /*
  @include vp-1439 {
    padding: 0 32px;
  }

  @include vp-1023 {
    padding: 0 24px;
  }
  */
}
@media (max-width: 767px) {
  .container {
    padding: 0 0.9375rem;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  padding: 1.875rem 0;
}
@media (max-width: 767px) {
  .header {
    padding: 1.375rem 0;
  }
}
.header .container {
  display: flex;
  align-items: center;
}
.header .logo {
  margin-right: 7.625rem;
}
@media (max-width: 767px) {
  .header .logo {
    margin-right: 4.0625rem;
  }
}
.header__nav {
  margin-right: 3.125rem;
}
@media (max-width: 767px) {
  .header__nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 1.25rem;
    visibility: hidden;
    opacity: 0;
    background-color: #2D69C3;
    pointer-events: none;
    transition: background-color 0.3s ease;
  }
}
.is-open .header__nav {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}
.is-open .header__nav .header__item {
  opacity: 1;
  transform: translateY(0);
}
.is-open .header__nav .header__button-start.btn {
  opacity: 1;
}
.header__nav .header__button-start.btn {
  display: none;
}
@media (max-width: 767px) {
  .header__nav .header__button-start.btn {
    display: inline-flex;
    margin: 0 auto;
    margin-top: auto;
    opacity: 0;
    transition: opacity 0.3s ease;
  }
}
.header__list {
  display: flex;
  gap: 1.5625rem;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .header__list {
    flex-direction: column;
    align-items: center;
    gap: 0.9375rem;
    margin: auto 0;
  }
}
@media (max-width: 767px) {
  .header__item {
    margin-bottom: 0;
    opacity: 0;
    transform: translateY(-25px);
    transition: opacity ease-out 0.3s, transform ease-out 0.3s;
  }
}
.header__link {
  padding: 0.25rem 0;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.375rem;
  color: #1E1E1E;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    color: #2D69C3;
  }
}
.header__link.focus-visible:focus {
  color: #2D69C3;
}
@media (max-width: 767px) {
  .header__link {
    color: #ffffff;
    font-size: 0.875rem;
    line-height: 1.0625rem;
  }
}
@media (max-width: 767px) and (hover: hover) {
  .header__link:hover:not(.focus-visible) {
    color: rgba(255, 255, 255, 0.5);
  }
}
@media (max-width: 767px) {
  .header__link.focus-visible:focus {
    color: rgba(255, 255, 255, 0.5);
  }
}
.header__burger {
  display: none;
  position: relative;
  z-index: 25;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  gap: 0.3125rem;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 0.3125rem;
  padding: 0;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__burger:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__burger.focus-visible:focus {
  opacity: 0.7;
}
.header__burger span {
  width: 1.75rem;
  height: 0.125rem;
  background-color: #2D69C3;
  transition: transform 0.6s ease, opacity 0.3s ease;
}
.is-open .header__burger span:nth-child(1) {
  transform: translateY(0.4375rem) rotate(225deg);
}
.is-open .header__burger span:nth-child(3) {
  transform: translateY(-0.4375rem) rotate(-225deg);
}
.header__burger span:nth-child(2) {
  width: 1.25rem;
}
.is-open .header__burger span:nth-child(2) {
  opacity: 0;
}
@media (max-width: 767px) {
  .header__burger {
    display: flex;
  }
}
.header__button-start.btn {
  margin-left: auto;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__button-start.btn:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__button-start.btn.focus-visible:focus {
  opacity: 0.7;
}
@media (max-width: 767px) {
  .header__button-start.btn {
    display: none;
  }
}
.header .toggle-language {
  margin-right: 1.875rem;
}
@media (hover: hover) {
  .header .toggle-language:hover:not(.focus-visible) .btn__caption {
    background-color: #2D69C3;
    color: #ffffff;
  }
}
.header .toggle-language.focus-visible:focus .btn__caption {
  background-color: #2D69C3;
  color: #ffffff;
}

@media (min-width: 768px) {
  .header--home .header__button-start .btn__icon {
    color: #2D69C3;
    background-color: #ffffff;
  }
  .header--home .header__button-start .btn__text {
    color: #ffffff;
  }
}

.main-nav__list {
  display: flex;
  margin: 0;
  margin-right: 20px;
  margin-left: 60px;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__link {
  color: #ffffff;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}

.footer {
  padding: 3.125rem 0;
  color: #ffffff;
  background-color: #2C2D32;
}
.footer a {
  color: #ffffff;
}
.footer .logo {
  width: 10.8125rem;
  height: 1.25rem;
  margin-bottom: 2rem;
  color: #ffffff;
}
@media (max-width: 767px) {
  .footer .logo {
    margin-bottom: 1.875rem;
  }
}
.footer__content {
  display: flex;
  margin-bottom: 2.5rem;
}
@media (max-width: 767px) {
  .footer__content {
    flex-direction: column;
  }
}
.footer__contacts-wrapper {
  width: 28.125rem;
  padding-right: 3.125rem;
}
@media (max-width: 767px) {
  .footer__contacts-wrapper {
    width: 100%;
    padding-right: 0;
    margin-bottom: 1.875rem;
  }
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  row-gap: 0.9375rem;
}
@media (max-width: 767px) {
  .footer__contacts {
    row-gap: 0.875rem;
  }
}
.footer__contacts a, .footer__contacts p {
  margin: 0;
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.1875rem;
}
@media (max-width: 767px) {
  .footer__contacts a, .footer__contacts p {
    font-size: 0.875rem;
    line-height: 1.0625rem;
  }
}
.footer__contacts a {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__contacts a:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.footer__contacts a.focus-visible:focus {
  opacity: 0.7;
}
.footer__navigation-wrapper {
  display: flex;
  width: 37.5rem;
}
@media (max-width: 767px) {
  .footer__navigation-wrapper {
    width: 100%;
    order: 2;
  }
}
.footer__navigation-wrapper ul {
  display: flex;
  flex-direction: column;
  row-gap: 1.25rem;
  width: 18.75rem;
  margin: 0;
  padding-left: 0;
  padding-right: 1.875rem;
  list-style: none;
}
@media (max-width: 767px) {
  .footer__navigation-wrapper ul {
    width: 10.25rem;
    row-gap: 1.5rem;
    padding-right: 0;
  }
}
.footer__navigation-wrapper ul:nth-child(2) {
  row-gap: 1.5625rem;
}
@media (max-width: 767px) {
  .footer__navigation-wrapper ul:nth-child(2) {
    width: 5.9375rem;
    row-gap: 1.5rem;
  }
}
.footer__navigation-wrapper li {
  display: flex;
}
.footer__navigation-wrapper a {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.1875rem;
  transition: color 0.3s ease;
}
@media (max-width: 767px) {
  .footer__navigation-wrapper a {
    font-size: 0.875rem;
    line-height: 1.0625rem;
  }
}
@media (hover: hover) {
  .footer__navigation-wrapper a:hover:not(.focus-visible) {
    color: #2D69C3;
  }
}
.footer__navigation-wrapper a.focus-visible:focus {
  color: #2D69C3;
}
.footer__action {
  display: flex;
  align-items: center;
  margin-bottom: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .footer__action {
    margin-bottom: 3.75rem;
    margin-left: 0;
  }
}
@media (hover: hover) {
  .footer__action:hover:not(.focus-visible) .footer__action-text {
    background-color: rgba(255, 255, 255, 0.2);
  }
}
.footer__action.focus-visible:focus .footer__action-text {
  background-color: rgba(255, 255, 255, 0.2);
}
.footer__action-text {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
  padding: 0.75rem 1.5rem 0.875rem 1.5rem;
  border-radius: 0.9375rem;
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.1875rem;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.1);
}
@media (max-width: 767px) {
  .footer__action-text {
    flex-grow: 1;
  }
}
.footer__action-icon {
  display: flex;
}
.footer__action-icon svg {
  width: 2.8125rem;
  height: 2.8125rem;
  border-radius: 50%;
}
.footer__info {
  display: flex;
  align-items: center;
}
.footer__info a {
  color: rgba(255, 255, 255, 0.5);
  transition: color 0.3s ease;
}
@media (hover: hover) {
  .footer__info a:hover:not(.focus-visible) {
    color: #ffffff;
  }
}
.footer__info a.focus-visible:focus {
  color: #ffffff;
}
@media (max-width: 767px) {
  .footer__info {
    flex-direction: column;
    gap: 1.25rem;
  }
}
.footer__info-designer {
  width: 28.125rem;
  padding-right: 3.125rem;
}
.footer__info-designer svg {
  width: 7.8125rem;
  height: 1.0625rem;
}
@media (max-width: 767px) {
  .footer__info-designer {
    width: 100%;
  }
}
.footer__offer, .footer__agreement {
  width: 18.75rem;
  padding-right: 1.875rem;
  font-size: 0.875rem;
